import { cn } from '@/lib/utils'
import { cva, VariantProps } from 'class-variance-authority'
import React from 'react'

const labelVariants = cva(
    "text-[16px] text-primary",
    {
        variants: {
            variant: {
                default: "bg-primary/10 rounded-md w-max-content py-1 px-3",
                secondary: "bg-primary text-white"
            }
        },
        defaultVariants: {
            variant: "default"
        }
    }
)

const Label = ({ className, variant, children, ...props}: React.ComponentProps<"label"> & VariantProps<typeof labelVariants>) => {
    return (
        <label className={cn(labelVariants({ variant, className}))}>
            {children}
        </label>
    )
}

export default Label